import React, { Component } from 'react'
import { Tabs } from 'antd';
import Onesingle from './onesingle';
import { Xfn } from '../../../../utils/axiosfn';
import { connect } from "react-redux";
import { FormattedMessage } from 'react-intl';
import { NavLink } from "react-router-dom"
import Bbbill from '../../../components/bbentrust/bbbill';
import Onesinglea from './onesingles';
import store from '../../../store';

const { TabPane } = Tabs;


@connect(
  state => {
    return {
      bbactive_order: state.bbdata.bbactive_order,
      qiehuan: state.bbdata.qiehuan,
      bbasset: state.bbdata.bbasset,
      bbaymbol: state.bbdata.bbaymbol,
    }
  }
)
class BbTablefoot extends Component {
  constructor() {
    super()
    this.state = {
      data: [],
      dataA: {},
      imgArr: {
        ioo: require("../../../img/nothing_data.png"),
      },
      datanull: '0',
      data3null: '0',
      data3: [],
      isofk: true
    }
  }
  bbbillHistory = (type) => {
    if (!localStorage.userInfo) {
      return false
    }
    Xfn({
      _u: 'bbtradequery',
      _m: 'get',
      _p: {
        asset: this.props.bbasset,
        symbol: this.props.bbaymbol,
        last_trade_id: type === 1 ? this.state.data3[this.state.data3.length - 1].id : "",
        next_page: "1",
        page_size: "20",
      }
    }, (res, code) => {

      if (code === 0) {
        var arr = res.data.data.rows ? res.data.data.rows : []
        for (var i in arr) {
          arr[i].key = arr[i] + i
        }
        if (type === 1) {
          arr = this.state.data3.concat(arr);
          if (res.data.data.rows === null || res.data.data.rows.length === 0) {
            this.setState({
              isofk: false
            })
          } else {
            this.setState({
              data3: arr,
              isofk: true,
              data3null: '1'
            })
          }
        } else {
          this.setState({
            data3: res.data.data.rows ? res.data.data.rows : [],
            data3null: '1',
            isofk: true,
          })
        }

      }

    })
  }

  componentDidUpdate() {
    if (this.props.qiehuan === 0) {
      this.setState({
        data3: null,
        data: null
      })
      this.history_data()
      this.bbbillHistory()
      store.dispatch({
        type: "qiehuandd"
      })
    }
  }
  dangqianchipang = (a, b) => {
    if (localStorage.userInfo) {
      if (b != "1" || a == null) {
        return <div style={{ position: "relative", zIndex: 999, width: "100%", textAlign: 'center', lineHeight: "200px", color: '#B8B8B8' }}>
          < FormattedMessage id="Loading" defaultMessage={'加载中'} />
          ...</div>
      } else {
        if (a <= 0) {
          return <div className="tablemeishuju">
            <img src={this.state.imgArr.ioo} alt="" />
            <div>
              < FormattedMessage id="You_dont_have_data" defaultMessage={'您暂时还没有相关数据'} />
            </div>
          </div>
        }
      }
    } else {
      return <div className="tablemeishuju">
        <img src={this.state.imgArr.ioo} alt="" />
        <div>
          < FormattedMessage id="You_must" defaultMessage={'您必须'} />
          <NavLink style={{ margin: "0 5px" }} to="/login"> < FormattedMessage id="Sign_in" defaultMessage={'登录'} /></NavLink>
          < FormattedMessage id="Only_then_see_information" defaultMessage={'才可以看到此信息'} />
        </div>
      </div>
    }
  }
  history_data = (url) => {
    if (!localStorage.userInfo) {
      return false
    }
    Xfn({
      _u: 'bborderquery_history',
      _m: 'get',
      _p: {
        asset: this.props.bbasset,// 资产 USD,必填,
        symbol: this.props.bbaymbol, //交易对,非必填,
        // bid_flag: '1', //1.买入,0.卖出,非必填,
        // last_order_id: 'id', //当前页最后一张委托的id, 非必填,
        next_page: "1", //翻页标记,-1 上一页 , 1.下一页 必填,,
        page_size: "20", //页行数 ,必填
      }
    }, (res, code) => {
      if (code === 0) {
        
        this.setState({
          data: res.data.data.rows ? res.data.data.rows : [],
          datanull: '1',
          dataA: res.data.data,
        })
      }
    })
  }
  callback = (value) => {
    if (localStorage.userInfo && value === '2') {
      this.history_data()
    } else if (value === '3') {
      this.bbbillHistory()
    }
  }
  //撤销淡定
  Cancel_order = (data) => {
    console.log(data)
    Xfn({
      _u: "bbordercancel",
      _m: "post",
      _p: {
        order_id: data.id,// 委托id,必填
        asset: data.asset,// 资产 USD,必填
        symbol: data.symbol,// 交易对,必填
        bid_flag: data.bid_flag,// 1.买入,0.卖出,必填
      }
    }, (res, code) => {

    }, '撤单成功')
  }
  componentDidUpdate(){
    if(this.props.bb_trade_exp_flg_data){
      store.dispatch({ type: "history_data", date: '' })
      this.setState({
        data: []
      })
      this.history_data()
     
    }
  }
  render() {
    const {
      data, datanull, dataA, data3, data3null, isofk
    } = this.state
    const {
      bbactive_order, bbaymbol, bbasset
    } = this.props
    return (
      <div className="bbtablefoot_warp">
        <h1 className='drag-handle hi-tiutel'></h1>
        <Tabs defaultActiveKey="1" style={{ height: "100%" }} onChange={this.callback} >
          <TabPane tab={"当前委托[" + (localStorage.userInfo ? bbactive_order.order_total ? bbactive_order.order_total : '--' : '0') + "]"} key="1">
            <div className=' overflow-auto-item' style={{ height: "100%" }}>
              {
                localStorage.userInfo && <Onesingle ctypeis='1' type='1'></Onesingle>
              }
              {this.dangqianchipang(bbactive_order.data && bbactive_order.data.length, '1')}
              <div className="bbtablefoot_warp_box g-scrollbar">
                {
                  localStorage.userInfo && bbactive_order.data && bbactive_order.data.map((itme, index) => {
                    return <Onesingle Cancel_order={this.Cancel_order} data={itme} type='1' key={index + itme}></Onesingle>
                  })
                }
                {
                  localStorage.userInfo && bbactive_order.data && bbactive_order.data.length > 0 ? <NavLink className="chak" to="/histororder/bbhistry">
                    查看更多
                </NavLink> : ''
                }
              </div>
            </div>
          </TabPane>

          <TabPane tab="历史委托" key="2">
            <div className="overflow-auto-item" style={{ height: "100%" }}>
              {
                localStorage.userInfo && <Onesingle ctypeis='1' type='2'></Onesingle>
              }

              {this.dangqianchipang(data, datanull)}

              <div className="bbtablefoot_warp_box g-scrollbar">
                {localStorage.userInfo && data && data.map((itme, index) => {
                  return <Onesingle Cancel_order={this.Cancel_order} data={itme} type='2' key={index + itme}></Onesingle>
                })}
                {
                  localStorage.userInfo && data && data.length > 0 ? <NavLink className="chak" to="/histororder/bbhistry">
                    查看更多
                </NavLink> : ''
                }

              </div>
            </div>
          </TabPane>
          <TabPane tab="成交明细" key="3" >
            <div className="overflow-auto-item" style={{ height: "100%" }}>
              {
                localStorage.userInfo && <Onesinglea ctypeis='1' type='3'></Onesinglea>
              }

              {this.dangqianchipang(data3, data3null)}

              <div className="bbtablefoot_warp_box g-scrollbar">
                {localStorage.userInfo && data3 && data3.map((itme, index) => {
                  return <Onesinglea Cancel_order={this.Cancel_order} data={itme} type='2' key={index + itme}></Onesinglea>
                })}
                {
                  localStorage.userInfo && data3 && data3.length > 0 ? isofk ? <div onClick={() => this.bbbillHistory(1)} className="chakjiazai">
                    加载更多
                </div> : <div className="chakjiazais" >
                      无更多数据
                </div> : ''
                }

              </div>
            </div>
          </TabPane>

        </Tabs>
      </div >
    )
  }
}
export default BbTablefoot