import React, { Component } from 'react'
import { connect } from "react-redux";
import store from '../../../store';
import { Xfn } from '../../../../utils/axiosfn';
import { bbsymbolfn } from '../../../action/bbtion';
import bbSubscribe from '../../../../utils/bb_send_unp';
import Subscribe from '@/utils/ws_sub_unsub';

let assetquanbu = null
var _that

@connect(
  state => {
    return {
      bbassetArr: state.bbdata.bbassetArr,
      bbasset: state.bbdata.bbasset,
      bbaymbol: state.bbdata.bbaymbol,
      bbsymbolArr: state.bbdata.bbsymbolArr,
      bbassetroute: state.bbdata.bbassetroute,
      bbinstrumentArr: state.bbdata.bbinstrumentArr,
      instrumentArr: state.data.instrumentArr,
      pcassetquery: state.data.pcassetquery,
      asset: state.data.asset,
      heyuename: state.data.heyuename,

    }
  }
)
class Headernav extends Component {
  constructor() {
    super()
    _that = this

    this.state = {
      imgArr: {
        a1: require("../../../img/rate_down.png"),
        a2: require('../../../img/rate_up.png'),
      },
      bbclkasset: "USDT",
      heyueclkasset: "BTC",
      bbclksybmol: "BTC/USDT",
      bbsyblarr: [], isOk: true
    }
  }
  componentDidMount() {
    this.bbassetFn()
  }

  bbassetFn = () => {
    Xfn({
      _u: "bbassetquery",
      _m: 'get',
      _p: {}
    }, (res, code) => {
      if (code === 0) {
        assetquanbu = 'USDT'
        Xfn({
          _u: "bbsymbolquery",
          _m: "get",
          _p: {
            asset: 'USDT'
          }
        }, (res, code) => {
          if (code === 0) {
            this.setState({
              bbsyblarr: res.data.data.rows
            })
          }
        })
      }
    })
  }
  dianji = (value) => {
    if (!this.state.isOk) {
      return false
    }

    assetquanbu = value
    const { _ctype } = this.props;
    if (_ctype === 'bb') {
      this.setState({
        bbclkasset: value,
        isOk: false
      })

      Xfn({
        _u: "bbsymbolquery",
        _m: "get",
        _p: {
          asset: value
        }
      }, (res, code) => {
        if (code === 0) {
          this.setState({
            bbsyblarr: res.data.data.rows,
            isOk: true
          })
        }
      })
    } else {
      this.setState({
        heyueclkasset: value,
        isOk: false
      })
      this.qiehuanusd(value)
    }

  }


  qiehuanusd = (a) => {
    Xfn({
      _u: 'pairQuery',
      _m: 'get',
      _p: {
        asset: a
      }
    }, (res, code) => {
      if (code === 0 && res.data.data.rows[0]) {
       
        Subscribe({
          _por: this.props,
          _this: this,
          _asset: a,
          _pair: res.data.data.rows[0].symbol,
          _typt: 9//切换资产
        })
        this.setState({
          isOk: true
        })
      }

    })
  }
  symbolfn = (value) => {
    store.dispatch({ type: "bbassetgaibaian", data: assetquanbu })
    store.dispatch({ type: 'bbsymbolgaibaian', data: value })
    bbSubscribe({
      bbaymbol: value,
      bbasset: assetquanbu,
      bbclksybmol: value
    })
  }
  SwitchingCurrencies = (a, b) => {
    if (a.symbol === this.props.heyuename) { return false }
    Subscribe({
      _por: this.props,
      _this: _that,
      _pair: a.symbol,
      _index: b,
      _type: "2",
      _asset: a.settle_currency,
    })
  }
  render() {
    const {
      imgArr, bbclkasset
    } = this.state
    const {
      bbassetArr, bbinstrumentArr, bbaymbol, instrumentArr, _ctype, pcassetquery, asset, heyuename
    } = this.props

    return (
      <div className="headernav_warp">
        <div className="nav">
          <div className="lie lie1">市场</div>
          <div className="lie lie2">币对</div>
          <div className="lie lie3">
            <span>
              涨幅
          </span>
            <span className='img_box'>
              <img className="img_box_d" src={imgArr.a1} alt="" />
              <img src={imgArr.a2} alt="" />
            </span>
          </div>
        </div>
        <div className="li_box">
          <ul className="box_bb box_bb1">
            {
              _ctype === 'bb' ?
                bbassetArr.map((item, index) => {
                  return <li key={item + index} onClick={() => this.dianji(item.asset)} className={bbclkasset !== item.asset ? "box_bbspan" : 'box_bbspan1 box_bbspan'}>{
                    item.asset
                  }</li>
                }) :
                pcassetquery.map((item, index) => {
                  return <li key={item + index} onClick={() => this.dianji(item.asset)} className={asset !== item.asset ? "box_bbspan" : 'box_bbspan1 box_bbspan'}>{
                    item.asset
                  }</li>
                })
            }
          </ul>
          <ul className="box_bb box_bb2">
            {
              _ctype === 'bb' ?
                bbinstrumentArr.map((item, index) => {
                  return <li className={bbaymbol !== item.symbol ? "" : 'box_bbspan999 '} key={item + index} onClick={() => this.symbolfn(item.symbol)}>
                    <span style={{
                      display: 'inline-block',
                      width: 114
                    }} > {
                        item.symbol
                      }</span>
                    <span className="Regular" style={{ color: item.change_rate_24h && item.change_rate_24h >= 0 ? "#26994E" : "#E53F39" }}>
                      {
                        item.change_rate_24h && item.change_rate_24h > 0 ? "+" + String(item.change_rate_24h * 100).replace(/^(.*\..{2}).*$/, "$1") : String(item.change_rate_24h * 100).replace(/^(.*\..{2}).*$/, "$1")
                      }
                    %
                  </span>
                  </li>
                }) : ''}
            {
              _ctype === 'heyue' ?
                instrumentArr.map((item, index) => {
                  if (asset !== item.settle_currency) {
                    return false
                  }
                  return <li className={heyuename !== item.symbol ? "" : 'box_bbspan999 '} key={item + index} onClick={() => this.SwitchingCurrencies(item, index)}>
                    <span style={{
                      display: 'inline-block',
                      width: 114
                    }} > {
                        item.symbol
                      }</span>
                    <span  className="Regular" style={{ color: item.change_rate_24h && item.change_rate_24h >= 0 ? "#26994E" : "#E53F39" }}>
                      {
                        item.change_rate_24h && item.change_rate_24h > 0 ? "+" + String(item.change_rate_24h * 100).replace(/^(.*\..{2}).*$/, "$1") : String(item.change_rate_24h * 100).replace(/^(.*\..{2}).*$/, "$1")
                      }
                    %
                  </span>
                  </li>
                }) : ''}
          </ul>
        </div>
      </div>
    )
  }
}

export default Headernav 
