import React, { Component } from 'react'
import { NavLink } from "react-router-dom"
import { FormattedMessage } from 'react-intl';
import store from '@/scripts/store'
import { history } from '@/utils/history'


export const HeaderNav = () => {
  const jiaoyiyemian = () => {
    store.dispatch({ type: "TransactionPage", TransactionPage: "1" })
    store.dispatch({ type: "isHistory", isHistory: 1 })
  }

  const liArr = [
    { text: <FormattedMessage id="home_page" defaultMessage={'首页'} />, routerurl: "/sices" },
    { text: <FormattedMessage id="Trade" defaultMessage={'合约交易'} />, routerurl: "/transaction" },
    { text: <FormattedMessage id="Currency_trading" defaultMessage={'币币交易'} />, routerurl: "/BBTradePage" },
    { text: 'C2C交易', routerurl: "/C2Cdeal" },
    { text: <FormattedMessage id="Invite_Commission" defaultMessage={'邀请返佣'} />, routerurl: "/fanyonganner" }
  ]

  return (
    <ul>
      {
        liArr.map((item, index) => {
          if (index === 1) {
            return <NavLink
              key={item + index}
              onClick={jiaoyiyemian}
              to={localStorage.userInfo ? '/transaction' : "/fulltrade"}
              className={window.location.hash.indexOf("cont") != -1 ? "navlinkuo selectedaaa" : "navlinkuo"}
              activeClassName="selectedaaa">
              <FormattedMessage id="Trade" defaultMessage={'合约交易'} />
              <span className="iconfont biaotiaicm">&#xe609;</span>
            </NavLink>
          }
          return <NavLink
            key={item + index}
            to={item.routerurl}
            className="navlinkuo"
            activeClassName="selectedaaa">
            {
              item.text
            }
          </NavLink>
        })
      }
    </ul>
  )
}


export const LeftNav = () => {
  const imgLoging = require("../../img/logo.png")

  return <div className="header-left clear">

    {/* imglogo */}
    <img onClick={() => history.push('/sices')} src={imgLoging} alt="" />

    {/* 导航栏 */}
    <HeaderNav></HeaderNav>

  </div>
}