import React, { Component } from 'react'
import './index.scss'
import Singlebox from './Singlebox'
import { history } from '@/utils/history'
import { Tabs } from 'antd';
import { Xfn } from '../../../../utils/axiosfn'
import { connect } from "react-redux";
const { TabPane } = Tabs;
@connect(
  state => {
    return {
      bbinstrument: state.bbdata.bbinstrument,
    }
  }
)
class Placeorder extends Component {
  constructor() {
    super()
    this.state = {
      getUserFeeObj: {},
      y: 1,
      list: false
    }
  }
  componentDidMount() {
    if (localStorage.userInfo) {
      Xfn({
        _u: "getUserFee",
        _m: "get",
        _p: {
        }
      }, (res, code) => {
        if (code == 0) {
          this.setState({
            getUserFeeObj: res.data.data
          })
        }
      })
    }
  }
  dianji = (type) => {
    this.setState({
      y: type,
      list: true
    })
  }
  render() {
    const {
      getUserFeeObj, y, list
    } = this.state
    const {
      bbinstrument
    } = this.props
    return (
      <div className="placeorder_warp">
        <h1 className="h1_title drag-handle" >
          <div className={'h1_dianji'} onClick={() => this.dianji(1)} style={{ color: y === 1 ? "#1890ff" : "", borderColor: y === 1 ? "#1890ff" : "" }}>{
            "买入" + (bbinstrument.symbol ? bbinstrument.symbol.split(bbinstrument.split_char)[0] : 'BTC')
          }</div>
          <div className={'h1_dianji'} onClick={() => this.dianji(2)} style={{ color: y !== 1 ? "#1890ff" : "", borderColor: y !== 1 ? "#1890ff" : "" }}> {
            "卖出" + (bbinstrument.symbol ? bbinstrument.symbol.split(bbinstrument.split_char)[0] : "BTC")
          }</div>
          <div className="feilv_box">
            <span className="fuhoa">
              %
           </span>
            <span className="feilv">费率</span>
            <div className="tankuang_box">
              <div className="tankuang_box_b">
                Maker {getUserFeeObj.maker_fee ? getUserFeeObj.bb_maker_fee + '%' : "--" + '%'} I Taker {getUserFeeObj.maker_fee ? getUserFeeObj.bb_taker_fee + '%' : '--' + '%'}
                <span className='fee' onClick={() => {
                  history.push('/personal/fxs/index')
                }}>手续费等级</span>
              </div>

            </div>
          </div>
        </h1>
        <div className="content_boxq">
          {
            y === 1 ? <Singlebox type='1' list={list} _this={this}></Singlebox> : <Singlebox list={list} _this={this} type='0'></Singlebox>
          }
        </div>

      </div>
    )
  }
}


export default Placeorder