import React, { Component } from 'react';
import './index.scss'
import { connect } from "react-redux";
import depthInit from '@/utils/depthmap'
import { FormattedMessage } from 'react-intl';
import store from '../../store';
const depthDatasFarms = function (data) {
    if (data.length < 1) return;
    var newArray = [];
    for (var i = 0; i < data.length; i++) {

        var itemArray = [];
        itemArray.push(parseFloat(data[i].price));
        itemArray.push(parseFloat(data[i].ljl));
        newArray.push(itemArray);

    }
    return newArray;
}
var flg = true
var timemeout
@connect(
    state => {
        return {
            orderBookL2_25obj: state.data.orderBookL2_25obj,
            orderBookL2_25: state.data.orderBookL2_25,
            bborder_book: state.bbdata.bborder_book,
            order_bookshu: state.bbdata.order_bookshu,
            onLayoutChangela: state.bbdata.onLayoutChangela,
            qiehuan: state.bbdata.qiehuan,
        }
    }
)
class Depthmap extends Component {
    constructor() {
        super()
        this.state = {
            depth_map: '',
        }
    }
    componentDidMount() {
        let asks = [];
        let bids = [];
        this.setState({
            depth_map: depthInit('depth_chart_container', asks, bids, 4)
        })
    }
    componentDidUpdate() {
        const { orderBookL2_25obj, bborder_book } = this.props;
        if (this.props.ctype === 'bb') {
            // if (this.props.qiehuan === 0) {
            //     let asks = [];
            //     let bids = [];
            //     this.setState({
            //         depth_map: depthInit('depth_chart_container', asks, bids, 4)
            //     })
            //     store.dispatch({
            //         type: "qiehuandd"
            //     })
            // }
            if (bborder_book.arrAsks) {
                let buy = bborder_book.arrAsks.length > 0 ? depthDatasFarms(bborder_book.arrAsks) : [];
                let sell = bborder_book.arrBids.length > 0 ? depthDatasFarms(bborder_book.arrBids).reverse() : [];
                this.state.depth_map.series[0].setData(buy);
                this.state.depth_map.series[1].setData(sell);
            }
        } else {
            if (orderBookL2_25obj.arrAsks) {
                let buy = orderBookL2_25obj.arrAsks.length > 0 ? depthDatasFarms(orderBookL2_25obj.arrAsks) : [];
                let sell = orderBookL2_25obj.arrBids.length > 0 ? depthDatasFarms(orderBookL2_25obj.arrBids).reverse() : [];
                this.state.depth_map.series[0].setData(buy);
                this.state.depth_map.series[1].setData(sell);
            }
        }
    }
    render() {
        return (
            <div id="depth_chart_container" style={{ height: "100%" }}>  <FormattedMessage id="Depth_map" defaultMessage={'深度图'} /></div>
        );
    }
}

export default Depthmap;