import React, { Component } from 'react'
import { Tabs } from 'antd';
import Bbentrust from '../../../components/bbentrust';
import Bbbill from '../../../components/bbentrust/bbbill';
import lang from '@/utils/language';
import { FormattedMessage } from 'react-intl';

const { TabPane } = Tabs;
export default class Bbhistry extends Component {
  constructor() {
    super()
    this.state = {
      key: ""
    }
  }
  callback = (key) => {
    this.setState(
      {
        key: key
      }
    )
  }
  render() {
    const {
      key
    } = this.state
    return (
      <div className="bbhistry_warp">
        <Tabs defaultActiveKey="1" onChange={this.callback}>
          <TabPane tab={<FormattedMessage id="Current_delegation" defaultMessage={'当前委托'} />} key="1">
            <Bbentrust type="0"></Bbentrust>
          </TabPane>
          <TabPane tab={<FormattedMessage id="Historical_entrustment" defaultMessage={'历史委托'} />} key="2">
            <Bbentrust type="1"></Bbentrust>
          </TabPane>
          <TabPane tab={'成交明细'} key="3">
            <Bbbill type="2"></Bbbill>
          </TabPane>
        </Tabs>
      </div>
    )
  }
}
