import React, { Component } from 'react'
import "./index.scss"
import store from '@/scripts/store'
import { connect } from "react-redux";
import { change_language, positionfunction, zhutiyanzheng } from "../../action";
import { FormattedMessage } from 'react-intl';
import { history } from '@/utils/history'
import Subscribe from '../../../utils/ws_sub_unsub';
import { Xfn } from '../../../utils/axiosfn';
import { downloadgeturlfn } from '../../action/tction';
import { LeftNav } from './nav'
import { RightMain } from './head_right';
@connect(
  state => {
    return {
      language: state.data.language,
      token: state.data.token,
      heyuename: state.data.heyuename,
      pcassetquery: state.data.pcassetquery,
      headerZX: state.datum.headerZX,
      asset: state.data.asset,
    }
  }
)
class Header extends Component {
  constructor() {
    super()
    this.state = {
      downloadapp: "https://www.gte.io/#/h5iosdownload",
      quanbu: true,
      information: [],
    }
  }
  componentDidMount() {

    this.changeLanguage(localStorage.language ? localStorage.language : "zh")
    Xfn({
      _u: 'information_url',
      _m: "get",
      _p: {
        type: "2",
        limit: "5"
      }
    }, (res, code) => {
      if (code == 0) {
        this.setState({
          information: res.data.data.rows
        })
      }
    })
    Xfn({
      _u: 'downloadget_url',
      _m: 'get',
      _p: {}
    }, (res, code) => {
      if (code == 0) {
        store.dispatch(downloadgeturlfn({
          downloadget_url: res.data.data,
          downloadget_url_flg: 1
        }))
        this.setState({
          downloadapp: res.data.data.h5_download_url
        })
      }
    })

  }
  tuichudenglu = () => {
    store.dispatch({ type: "TransactionPage", TransactionPage: "1" })
    store.dispatch(positionfunction([]))
    document.getElementsByTagName("body")[0].className = "theme-dark"
    this.setState({
      zhuti: "dark"
    })
    localStorage.theme = "dark"
    store.dispatch(zhutiyanzheng('dark', 1))
    localStorage.userInfo = "";
    localStorage.userName = "";
    history.push('/transaction')
  }
  tiaozhuan = () => {
    history.push('/personal')
  }
  atz = () => {
    history.push('/personal/security')
  }
  changeLanguage = (val) => {
    if (!window.zEzn) {
      return false
    }

    if (val == 'zh') {
      window.zEzn()
    } else {
      window.zEen()
    }
    store.dispatch(change_language(val));
    localStorage.setItem('language', val);
  }


  // 切换资产
  qiehuanusd = (a) => {
    Xfn({
      _u: 'pairQuery',
      _m: 'get',
      _p: {
        asset: a
      }
    }, (res, code) => {
      if (code === 0 && res.data.data.rows[0]) {
        Subscribe({
          _por: this.props,
          _this: this,
          _asset: a,
          _pair: res.data.data.rows[0].symbol,
          _typt: 9//切换资产
        })
      }
    })
  }
  render() {
    const {
      language,
      pcassetquery,
      asset
    } = this.props
    return (
      <div className="header-warp">

        {/* 左侧 */}
        <LeftNav></LeftNav>

        {/* 右侧 */}
        <RightMain
          information={this.state.information}
          downloadapp={this.state.downloadapp}
          language={language}
          asset={asset}
          pcassetquery={pcassetquery}
          qiehuanusd={this.qiehuanusd}
          tuichudenglu={this.tuichudenglu}
          atz={this.atz}
        ></RightMain>
      </div >
    )
  }
}
export default Header
